import { Component, OnInit } from '@angular/core';
import { MyServiceService } from '../my-service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-signs-up',
  templateUrl: './signs-up.component.html',
  styleUrls: ['./signs-up.component.css']
})
export class SignsUpComponent implements OnInit {

  usr=""
  pass=""
  pass1=""

  username:any;
  password:any;
  password1:any;

  constructor(private service:MyServiceService,private route: Router) { }

  onClick(){
    this.username = this.usr
    this.password = this.pass
    this.password1 = this.pass1

    if(this.password == this.password1){
    let response = this.service.setUserName(this.username,this.password)
    response.subscribe(data=>{
      // console.log(data) 
    })

      alert('SignUp Sucessfully')
      this.route.navigateByUrl('login')
    }
    else{
      alert('Renter-Password')
    }
  }
  
  ngOnInit() {
  }

}
