import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { City } from './city';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MyServiceService {

  constructor(private http:HttpClient) { }

  public getCity():Observable<any>{
    return this.http.get("http://localhost:8081/cityGetAll");
  }

  public createCity(city:any):Observable<any>{
    return this.http.post("http://localhost:8081/cityCreate",city , {responseType: "text" as "json"});
  }

  public deleteCity(city:any):Observable<any>{
    return this.http.get(`http://localhost:8081/cityDelete?cityName=${city}`,{responseType: 'text' as 'json'});
  }

  public getCategory():Observable<any>{
    return this.http.get("http://localhost:8081/categoryGetAll");
  }

  public createCategoty(catgory:any):Observable<any>{
    return this.http.post("http://localhost:8081/categoryCreate",catgory, {responseType: "text" as "json"});
  }

  public deleteCategory(category:any):Observable<any>{

    return this.http.get(`http://localhost:8081/categoryDelete?categoryName=${category}`,{responseType: "text" as "json"});
  }

  public createUserProfile(personal,working,add,cont,cat):Observable<any>{
      
    return this.http.get(`http://localhost:8081/createProfile?profileDetail=${personal}&workingDays=${working}&address=${add}&contact=${cont}&category=${cat}`,{responseType: "text" as "json" });
  }

  public getUserProfile(cat:any):Observable<any>{
    let param1 = new HttpParams().set("category",cat)
    return this.http.get("http://localhost:8081/profile/getCat",{params:param1});
  }
    
  public createAdminProfile(cat,cit,em,pas,tit):Observable<any>{
   
    return this.http.get(`http://localhost:8081/adminRegCreate?category=${cat}&city=${cit}&email=${em}
    &password=${pas}&title=${tit}`,{responseType: "text" as "json"});
  }

  public getAdminProfile(cat:any):Observable<any>{
    let param1 = new HttpParams().set("category",cat)
    return this.http.get("http://localhost:8081/admin/getCat",{params:param1});
  }

  public getUserName(username:any):Observable<any>{
    
    return this.http.get(`http://localhost:8081/login/username?username=${username}`);
  }
 
  public setUserName(username:any,password:any):Observable<any>{
    
    return this.http.get(`http://localhost:8081/login/create?username=${username}&password=${password}`,{responseType: 'text' as 'json'});
  }

  public updatePass(username:any,password:any):Observable<any>{
    
    return this.http.get(`http://localhost:8081/update/password?username=${username}&password=${password}`,{responseType: 'text' as 'json'});
  }

}
