import { Component, OnInit } from '@angular/core';
import { MyServiceService } from '../my-service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.css']
})
export class AdminLoginComponent implements OnInit {

  usr=""
  pass=""

  username:any;
  password:any;

  constructor(private service:MyServiceService,private route: Router) { }

  onClick(){
    this.username = this.usr
    this.password = this.pass

    let response = this.service.getUserName(this.username)
    response.subscribe(data=>{
      // console.log(data)

      if(data==null){
        alert("Wrong UserName")
      }
      else if(this.password != data.password){
        alert("Password is Wrong")
      }
      else{
        this.route.navigateByUrl("city")
      }

  }
    )
  }

  ngOnInit() {
  }

}
