import { Component, OnInit } from '@angular/core';
import { MyServiceService } from '../my-service.service';

@Component({
  selector: 'app-register-business',
  templateUrl: './register-business.component.html',
  styleUrls: ['./register-business.component.css']
})
export class RegisterBusinessComponent implements OnInit {

  pass=""
  tit=""
  em=""

  letCity:any[];
  letCategories:any[];
  email:any;
  password:any;
  title:any;
  selectedCat:any;
  selectedCity:any;

  constructor(private service:MyServiceService ) { }

  onCatChange(event){
    const value = event.target.value;
    this.selectedCat = value;
    console.log(this.selectedCat);
  }

  onCityChange(event){
    const value = event.target.value;
    this.selectedCity = value;
    // console.log(this.selectedCity);
  }

  onClick(){
    this.email = this.em
    this.password = this.pass
    this.title = this.tit
    this.service.createAdminProfile(this.selectedCat,this.selectedCity,this.email,this.password,this.title).
    subscribe()
  }

  ngOnInit() {

    let response = this.service.getCity()
    response.subscribe(
    data =>{
        this.letCity = data;
        // console.log(this.letCity); 
        
    })

    let response1 = this.service.getCategory()
    response1.subscribe(
    data =>{
        this.letCategories = data;
        // console.log(this.letCategories); 
    })

  }

}
